import Flickity from 'flickity';
import './components/index.js'; // Правильный путь к вашему компоненту






if (location.pathname.includes('/facts.html')) {
  var elem = document.querySelector('.values__slider');
  var flkty = new Flickity(elem, {
    cellAlign: 'left',
    contain: true,
    prevNextButtons: false,
    pageDots: false,
    wrapAround: true,
  });

  var buttonLeft = document.querySelector('.round-button--left');
  var buttonRight = document.querySelector('.round-button--right');
  var buttonLeftMobile = document.querySelector('.round-button--mobile');
  var buttonRightMobile = document.querySelector('.round-button--phone');

  // Function to handle slide navigation to the previous slide
  function navigateToPreviousSlide() {
    flkty.previous();
  }

  // Function to handle slide navigation to the next slide
  function navigateToNextSlide() {
    flkty.next();
  }

  // Click event listeners for arrow buttons on desktop
  buttonLeft.addEventListener('click', navigateToPreviousSlide);
  buttonRight.addEventListener('click', navigateToNextSlide);

  // Touch event listeners for arrow buttons on mobile devices
  buttonLeftMobile.addEventListener('touchend', navigateToPreviousSlide);
  buttonRightMobile.addEventListener('touchend', navigateToNextSlide);
}

if (location.pathname.includes('/history.html')) {
  // Получаем кнопку и поп-ап по классам
  const openButton = document.querySelector('.history__bta');

  // Получаем кнопку "Закрыть" по классу
  const closeButton = document.querySelector('.popup__close');

  // Получаем поп-ап по классу
  const popup = document.querySelector('.popup');

  // Обработчик нажатия на кнопку "УЗНАТЬ"
  openButton.addEventListener('click', () => {
    // Добавляем класс popup--open, чтобы открыть поп-ап
    popup.classList.add('popup--open');
  });

  // Обработчик нажатия на кнопку "Закрыть"
  closeButton.addEventListener('click', () => {
    // Удаляем класс popup--open, чтобы закрыть поп-ап
    popup.classList.remove('popup--open');
  });

}


