if (location.pathname.includes('/pay.html')) {
  const checkbox1 = document.getElementById("checkbox");
  const initialElement1 = document.querySelector(".pincod__initial--sub");
  let isVisible1 = false;

  checkbox1.addEventListener("click", function () {
      if (!isVisible1) {
          initialElement1.style.display = "block";
          isVisible1 = true;
      } else {
          initialElement1.style.display = "none";
          isVisible1 = false;
      }
  });

  const checkbox2 = document.getElementById("checkbox2");
  const initialElement2 = document.querySelector(".pincod__initial--s");
  let isVisible2 = false;

  checkbox2.addEventListener("click", function () {
      if (!isVisible2) {
          initialElement2.style.display = "block";
          isVisible2 = true;
      } else {
          initialElement2.style.display = "none";
          isVisible2 = false;
      }
  });
}

